.profile__interest {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 415px;
  width: 100%;
}

@media screen and (max-width: 768px) {
  .profile__interest {
    text-align: center;
  }
}

@media screen and (max-width: 320px) {
  .profile__interest {
    font-size: 14px;
    line-height: 17px;
  }
}
