.header {
  max-width: 880px;
  max-height: 74px;
  padding: 45px 0 0;
  margin: 0 auto;
  text-align: left;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media screen and (max-width: 320px) {
  .header {
    padding: 28px 27px 0;
    min-height: 56px;
  }
}
